<template>
    <div class="self-leaflet-edit-layer"></div>
</template>

<script>
    import { ImageOverlayRotated } from '../map/Leaflet.ImageOverlay.Rotated.js';
    import editLayerMixin from '../mixins/editLayerMixin.js';
    import drawMixin from '../mixins/drawMixin.js';

    export default {
        mixins: [editLayerMixin, drawMixin],
        name: 'WlEditLayer',
        data() {
            return {
                editable: false,
                editTypeArr: ['rectangle', 'polygon', 'circle', 'polyline'],
                editType: 'polygon', //rectangle
                drawLayer: null,
                drawShapeMore: false, //,
                isMeasure: false, //
                drawStyle: { color: '#4c7efd', weight: 1 }, //
            }
        },
        methods: {
            // 
            setEditType(type, isMeasure = false) {
                if (this.editTypeArr.includes(type)) {
                    this.editType = type;
                    this.setIsMeasure(isMeasure);
                } else {
                    alert('');
                }
            },
            //   editable
            setEditable(editable) {
                this.clearMarker();
                this.editable = editable;
            },
            // 
            getEditLayer() {
                return this.editLayer;
            },
            // 
            getDrawLayer() {
                return this.drawLayer;
            },
            // 
            clearEditLayers() {
                this.editLayer.clearLayers();
            },
            // 
            fitBounds(geojsonLayer) {
                this.$nextTick(() => {
                    this.parentMap.fitBounds(geojsonLayer.getBounds())
                });
            },
            // geoJson
            setGeojsonParams(...params) {
                let geojsonLayer = L.geoJson(...params);
                geojsonLayer.addTo(this.editLayer);
                return geojsonLayer;
            },
            // 
            setImageOverlayRotated(imgSrc, topleft, topright, bottomleft, options) {
                let point1 = L.latLng(topleft);
                let point2 = L.latLng(topright);
                let point3 = L.latLng(bottomleft);
                // let imageLayer = ImageOverlayRotated(imgSrc, topleft, topright, bottomleft, options);
                let imageLayer = ImageOverlayRotated(imgSrc, point1, point2, point3, options);
                imageLayer.bringToFront();
                imageLayer.addTo(this.editLayer);
                return imageLayer;
            },
            /**
             * 
             */
            setDrawStyle(obj) {
                this.drawStyle = Object.assign({}, this.drawStyle, obj);
            },
            resetDrawStyle() {
                this.drawStyle = { color: '#4c7efd', weight: 1 };
            },
            // 
            setIsMeasure(isMeasure) {
                this.isMeasure = isMeasure;
            },
            // 
            handleInitDrawShapeStatus(tempDrawing) {
                let shapeLayer = tempDrawing;
                this.$emit('emitDrawShapeFinish', shapeLayer);
            },
            // 
            setDrawShapeMore(bool) {
                this.drawShapeMore = bool;
            },
            // 
            emitRectangleStart(rectangleLayer) {
                this.$emit('emitRectangleStart', rectangleLayer);
            },
            // 
            emitRectangleFinish(rectangleLayer) {
                this.$emit('emitRectangleFinish', rectangleLayer);
            },

        },
        mounted() {
            this.parentMap.on('click', this.onMapClick);
            this.parentMap.on('mousemove', this.onMapMousemove);
            // this.parentMap.on('mouseup', this.onMapMouseup);
        }
    }
</script>

